/**
 * Configuraes de validao
 */
export const VALIDATION_RULES = {
  cnpj: {
    required: true,
    minLength: 14,
    pattern: /^\d{2}\.\d{3}\.\d{3}\/\d{4}-\d{2}$/,
  },
  cep: {
    required: true,
    minLength: 8,
    pattern: /^\d{5}-?\d{3}$/,
  },
  email: {
    required: true,
    pattern: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
  },
  telefone: {
    required: false,
    pattern: /^\(\d{2}\)\s\d{4,5}-\d{4}$/,
  },
} as const;
